import { createCard } from "./Card";

export default {
  title: "Component/Card",
  argTypes: {
    color: { control: "color" },
  },
};

const Template = (args) => {
  const card = createCard(args);
  const container = document.createElement("div");
  container.appendChild(card);
  return container;
};

export const Default = Template.bind({});
Default.args = {
  color: "black",
  background: "blue",
};
